//
// Color system
//

$white:     #fff !default; // bootstrap default
$gray-700:  #43536d !default;
$gray-100:  lighten($gray-700, 60%) !default; // #eef0f4
$gray-200:  lighten($gray-700, 50%) !default; // #ced5e1
$gray-300:  lighten($gray-700, 40%) !default; // #aebace
$gray-400:  lighten($gray-700, 30%) !default; // #8f9fba
$gray-500:  lighten($gray-700, 20%) !default; // #6f84a7
$gray-600:  lighten($gray-700, 10%) !default; // #566b8d
$gray-800:  darken($gray-700, 10%) !default; // #303b4d
$gray-900:  darken($gray-700, 20%) !default; // #1c232e
$black:     #000 !default; // bootstrap default

$grays: (
  100: $gray-100,
  200: $gray-200,
  300: $gray-300,
  400: $gray-400,
  500: $gray-500,
  600: $gray-600,
  700: $gray-700,
  800: $gray-800,
  900: $gray-900
) !default;

$blue:     #005a93; // custom
$mid-blue: #0071c4; // custom
$indigo:   #6610f2 !default; // bootstrap default
$purple:   #6f42c1 !default; // bootstrap default
$pink:     #e83e8c !default; // bootstrap default
$red:      #da273b !default; // custom
$orange:   #fd7e14 !default; // bootstrap default
$yellow:   #ffc107 !default; // bootstrap default
$green:    #008a00 !default; // custom
$teal:     #20c997 !default; // bootstrap default
$cyan:     #29abe2 !default; // custom

$colors: (
  blue: $blue,
  indigo: $indigo,
  purple: $purple,
  pink: $pink,
  red: $red,
  orange: $orange,
  yellow: $yellow,
  green: $green,
  teal: $teal,
  cyan: $cyan,
  white: $white,
  gray: $gray-600,
  gray-dark: $gray-800,
);

$theme-colors: (
  primary: $green, // custom
  secondary: $gray-600, // bootstrap default
  success: $green, // bootstrap default
  info: $mid-blue, // bootstrap default
  warning: $yellow, // bootstrap default
  danger: $red, // bootstrap default
  light: $gray-100, // bootstrap default
  dark: $gray-800 // bootstrap default
);

// Set a specific jump point for requesting color jumps
//$theme-color-interval: 8% !default;


// Options
//
// Quickly modify global styling by enabling or disabling optional features.

//$enable-rounded:            true !default;
$enable-shadows:            true;
//$enable-gradients:          false !default;
//$enable-transitions:        true !default;
//$enable-hover-media-query:  false !default;
//$enable-grid-classes:       true !default;
//$enable-print-styles:       true !default;


// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

$spacer: 1.2308rem; // $font-size-root (13px) * $spacer = 16px
$spacers: (
  0: 0,
  1: ($spacer * .25),
  2: ($spacer * .5),
  3: $spacer,
  4: ($spacer * 1.5),
  5: ($spacer * 3),
  lg: $spacer * 1.5, // 24px - custom
  sm: $spacer * .75, // 12px - custom
  xs: $spacer * .625 // 10px - custom
) !default;

// This variable affects the `.h-*` and `.w-*` classes.
//$sizes: (
//  25: 25%,
//  50: 50%,
//  75: 75%,
//  100: 100%
//) !default;

// Body
//
// Settings for the `<body>` element.

$body-bg: lighten($gray-100, 4);
$body-color: $gray-700; // eg. Text, labels, iconography

// Links
//
// Style anchor elements.

$link-color: #0071c4;
